import React, {useState, useEffect} from 'react';
import {toast} from 'react-toastify';
import ChoiceQuestion from '../ChoiceQuestion';
import {Row, Col, Button, FormGroup, Label, Input} from 'reactstrap';

import "./styles.css";

function TCLE({previousStep, nextStep, changeData, initialState}) {
  const [textInputs, setTextInputs] = useState({
    nome: initialState['nome'] || '',
    rg: initialState['rg'] || '',
    idade: initialState['idade'] || '',
  }); 
  const questions = [
    'Declaro minha participao',
  ];
  const [values, setValues] = useState([
    initialState[questions[0]] || null,
  ]);
  const options = [
    [
        'Sim',
        'No',
    ]
  ];

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []); 

  function handleChangeInput(field, value) {
    setTextInputs({
        ...textInputs,
        [field]: value,
    });
  }

  function handleChangeValue(questionIndex, questionValue) {
    const newValues = values.map((value, valueIndex) => {
        if (questionIndex === valueIndex) {
            return questionValue;
        }
        return value;
    });
    setValues(newValues);
  }

  function customValidations() {
    if (values[0] === 'No') {
      toast.error(`Para participar da pesquisa, voc precisa declarar o seu aceite no termo de consentimento.`);
      return false;
    } 
    return true;
  }

  function validateData() {
    let valid = true;
    if (textInputs.nome.trim() === '') {
        toast.error(`Por favor, responda a pergunta 1: "Nome"`);
        valid = false;
    } else if (textInputs.idade.trim() === '') {
        toast.error(`Por favor, responda a pergunta 2: "Idade"`);
        valid = false;
    } else if (textInputs.rg.trim() === '') {
        toast.error(`Por favor, responda a pergunta 3: "RG"`);
        valid = false;
    } else {
        try {
            const intAge = parseInt(textInputs.idade);
            if (intAge === undefined || isNaN(intAge)) {
                throw Error();
            }
        } catch (err) {
            toast.error(`A idade inserida  invlida`);
            valid = false;
        }
    }
    if (!valid) return false;

    for (let i = 0; i < values.length; i++) {
      const value = values[i];
      if (value === null) {
        toast.error(`Por favor, responda a pergunta ${i+4}: "${questions[i]}"`);
        valid = false;
        break;
      }
    }

    if (!valid) return false;
    
    valid = customValidations();

    return valid;
  }

  function handleSubmit() {
    const dataOK = validateData();

    if (dataOK) {
      let data = {
          ...textInputs,
      };
      for (let i = 0; i < questions.length; i++) {
        const question = questions[i];
        data = {
          ...data,
          [question]: values[i]
        }
      }

      changeData(data);
      nextStep();
    }
  }

  return (
    <div>
        <h3>Termo de Consentimento Livre e Esclarecido -  Intrinsic Motivation Inventory Task Evaluation Questionnaire (IMI-TEQ)</h3>
        <p>Eu estou sendo convidado a participar do estudo denominado "Validao da traduo do questionrio Intrinsic Motivation Inventory (IMI) Task Evaluation Questionnaire para o portugus brasileiro", cujos objetivos e justificativas so: validar a traduo do questionrio Intrinsic Motivation Inventory Task Evaluation Questionnaire (IMI-TEQ) para a lngua portuguesa e sua adaptao transcultural ao contexto brasileiro. O IMI  um questionrio produzido e validado na lngua inglesa e  usado em diversas pesquisas de diferentes reas (ex. educao, sade, tecnologia) para medir a motivao intrnseca, explicada pela teoria da autodeterminao como sendo comportamentos que so motivados pelo interesse da pessoa, apenas pelo sentimento de satisfao. O IMI-TEQ  uma verso menor do questionrio IMI, com 22 itens, usado principalmente para medir a motivao intrnseca e auto-regulao em tarefas genricas. A traduo e validao do IMI-TEQ para pesquisas no Brasil  necessria visto que a utilizao de questionrios em diferentes idiomas pode comprometer a validade de pesquisas cientficas. Na falta de questionrios traduzidos validados, os pesquisadores criam tradues livres, o que pode levar a inconsistncias e perda de validade dos resultados.</p>
        <p>A minha participao no referido estudo ser no sentido de fornecer sugestes e opinies acerca da compreenso dos itens do IMI-TEQ traduzidos para o portugus brasileiro. Para isso, eu fornecerei dados sociodemogrficos (ex. idade, escolaridade, gnero, ocupao), descreverei uma experincia  minha escolha com um produto digital que considerei motivadora ou desmotivadora, avaliarei a experincia descrita utilizando o IMI-TEQ traduzido,  responderei a um questionrio para medir a minha compreenso acerca dos itens do questionrio IMI-TEQ traduzido e, por fim, participarei de uma entrevista para fornecer impresses sobre palavras, frases ou expresses utilizadas na traduo. O tempo estimado para a participao da pesquisa  em torno de 20 minutos.</p>
        <p>Fui alertado de que, da pesquisa a se realizar, posso esperar alguns benefcios, tais como: </p>
        <p>- Contribuir com a cincia, ajudando na melhoria e validao de um instrumento para a comunidade de pesquisa brasileira, que pode ser utilizado em diversas reas, como educao, sade e tecnologia, para medir conceitos relacionados  teoria da autodeterminao (motivao intrnseca e auto-regulao).</p>
        <p>- Fomentar o desenvolvimento de pesquisas e produtos que, utilizando o IMI-TEQ traduzido, sero criados levando em considerao fatores que motivam e engajam seu pblico-alvo, inclusive, potencialmente, a mim.</p>
        <p>Recebi, por outro lado, os esclarecimentos necessrios sobre os possveis desconfortos e riscos decorrentes do estudo, levando-se em conta que  uma pesquisa, e os resultados positivos ou negativos somente sero obtidos aps a sua realizao. Os possveis riscos durante este processo so:</p>
        <p>- Cansao ou aborrecimento ao responder os questionrios;</p>
        <p>- Cansao ou vergonha ao responder s perguntas;</p>
        <p>- Constrangimento ao expor a opinio durante a realizao das entrevistas;</p>
        <p>- Desconforto, constrangimento ou alteraes de comportamento durante gravaes de udio e vdeo;</p>
        <p>- Possibilidade de constrangimento ao responder o instrumento de coleta de dados; Medo de no saber responder ou de ser identificado.</p>
        <p>Estou ciente de que minha privacidade ser respeitada, ou seja, meu nome ou qualquer outro dado ou elemento que possa, de qualquer forma, me identificar, ser mantido em sigilo.</p>
        <p>Tambm fui informado de que posso me recusar a participar do estudo, ou retirar meu consentimento a qualquer momento, sem precisar justificar, e de, por desejar sair da pesquisa, no sofrerei qualquer prejuzo  assistncia que venho recebendo.</p>
        <br />
        <p><b>Endereo dos responsveis pela pesquisa:</b></p>
        <div className="small-texts">
          <p>Nome: Ticianne de Gois Ribeiro Darin e Caio Eduardo Pereira Nunes</p>
          <p>Instituio: Universidade Federal do Cear</p>
          <p>Endereo: Campus do Pici - Bloco 1430 CEP 60.440-554 Fortaleza - CE</p>
          <p>Telefones para contato: (85) 85996181086</p>
          <p>ATENO: Se voc tiver alguma considerao ou dvida, sobre a sua participao na pesquisa, entre em contato com o Comit de tica em Pesquisa da UFC/PROPESQ  Rua Coronel Nunes de Melo, 1000 - Rodolfo Tefilo, fone: 3366-8344/46. (Horrio: 08:00-12:00 horas de segunda a sexta-feira).</p>
          <p>O CEP/UFC/PROPESQ  a instncia da Universidade Federal do Cear responsvel pela avaliao e acompanhamento dos aspectos ticos de todas as pesquisas envolvendo seres humanos.</p>
        </div>
        <br />
        <FormGroup>
            <Label for="nome">
            1. Nome <span>*</span>
            </Label>
            <Input id="nome" value={textInputs.nome} onChange={({currentTarget}) => handleChangeInput('nome', currentTarget.value)} />
        </FormGroup>
        <br />
        <FormGroup>
            <Label for="idade">
            2. Idade <span>*</span>
            </Label>
            <Input id="idade" value={textInputs.idade} onChange={({currentTarget}) => handleChangeInput('idade', currentTarget.value)} />
        </FormGroup>
        <br />
        <FormGroup>
            <Label for="rg">
            2. RG <span>*</span>
            </Label>
            <Input id="rg" value={textInputs.rg} onChange={({currentTarget}) => handleChangeInput('rg', currentTarget.value)} />
        </FormGroup>
        <br />
        <p>Eu declaro que  de livre e espontnea vontade que estou como participante de uma pesquisa. Eu declaro que li cuidadosamente este Termo de Consentimento Livre e Esclarecido e que, aps sua leitura, tive a oportunidade de fazer perguntas sobre o seu contedo, como tambm sobre a pesquisa, e recebi explicaes que responderam por completo minhas dvidas. E declaro, ainda, estar recebendo uma via assinada deste termo.</p>
        {questions.map((question, index) => (
          <ChoiceQuestion
            key={index}
            number={index+3}
            question={question}
            values={options[index]}
            currentValue={values[index]}
            onChange={(value) => handleChangeValue(index, value)}
          />
        ))}
        <Row sm={12}>
          {previousStep && (
            <Col md={10} sm={8} xs={6}>
              <Button onClick={previousStep}>Anterior</Button>
            </Col>
          )}
          {nextStep && (
            <Col md={2} sm={4} xs={6}>
              <Button onClick={handleSubmit}>Prximo</Button>
            </Col>
          )}
        </Row>
    </div>
  );
}

export default TCLE;